.page-login {
    position: relative;

    .full-screen {
        position: fixed;
        padding: 215px 0 135px;
        width: 100%;
        height: 100%;
        top: 0px;
        overflow-y: auto;

        ._col-box {
            @media (max-width: 769px) {
                width: 80%;
            }

            @media (max-width: 601px) {
                width: 100%;
            }

            .mob-login {
                @media (max-width: 441px) {
                    padding-left: 15px !important;
                    padding-right: 15px !important;
                }

                .content {
                    padding: 1.5rem;
                    @media (max-width: 441px) {
                        padding-bottom: 0;
                    }

                    .ant-form-item.mb-075 {
                        margin-bottom: 0.75rem !important;
                    }
                }
            }
        }
    }

    #btnLottery {
        @media (max-width: 601px) {
            padding: 10px 0;
            margin: 0;
        }
    }
}
